import { arrayMove, cx, get, set } from '@elliemae/ds-utilities/utils';
import { mergeRefs } from '@elliemae/ds-utilities/system';
import { cloneDeep } from '../../../../../ds-utilities/src/utils';

export function getStyle(provided, style) {
  if (!style) {
    return provided.draggableProps.style;
  }
  return {
    ...provided.draggableProps.style,
    ...style,
  };
}

export function getDraggableProps(props, provided, snapshot) {
  const isGroupedOver = Boolean(snapshot.combineTargetFor);
  return {
    ...props,
    ...provided.draggableProps,
    className: cx(props.className, isGroupedOver && 'can-group-over'),
    // provided,
    innerRef: mergeRefs(provided.innerRef, props.innerRef, props.ref),
    'data-index': props.index,
    style: getStyle(provided, { ...props.style, display: 'flex' }),
  };
}

export const setTreeArrayInPath = (treeData, path, data) => {
  const nextTreeData = [...treeData];
  if (path.length) {
    set(nextTreeData, path, data);
    return nextTreeData;
  }
  return data;
};

// check if at the earliest possible different nest level the source is before the target or not
export const isSourceBeforeTarget = (sourcePath, targetPath) => {
  let sourceIsBeforeTarget = false;
  if (sourcePath.length <= targetPath.length) {
    // we increase by 2 beacuse every other position is 'children'
    for (let i = 0; i < sourcePath.length; i += 2) {
      const source = sourcePath[i];
      const target = targetPath[i];
      // since we skip by 2 source/target can be out of bounds
      // if source/target are the same we are at same nest level
      if (
        !Number.isInteger(source) || // check to see if it's a number because 0 == false in javascript
        !Number.isInteger(target) ||
        source === target
      )
        continue;
      // the nest level differs, as such we check if source if before or after
      sourceIsBeforeTarget = source < target;
      break;
    }
  }

  return sourceIsBeforeTarget;
};

export const updateTargetPath = (sourcePath, targetPath, incr = -1) => {
  const nextTargetPath = [...targetPath];

  const posToUpdate = sourcePath.length ? sourcePath.length - 1 : 0;
  nextTargetPath.splice(posToUpdate, 1, targetPath[posToUpdate] + incr);

  return nextTargetPath;
};

export const getParentPath = (path) => path.slice(0, -1);
export const getParentChildrenByPath = (treeData, path) =>
  path && path.length ? get(treeData, path) : treeData;

export const getTreePosition = (recordPath) => ({
  parentPath: getParentPath(recordPath),
  index: recordPath.slice(-1)[0],
});

export const changePositionBetweenDifferentArrays = ({
  sourceTreePosition, // already extracted sourceRecord.path
  targetTreePosition, // already extracted targetRecord.path
  // combineWith the element we want to append to the children of (this is null when the drag doesn't result in adding children)
  combineWith,
  sourceRecord, // what we are dragging
  targetRecord, // what we are dropping on top of
  down, // ???
  data, // tree before dragEnd changes
  childrenKey = 'children', // constant -> 'children'
}) => {
  let nextTreeData = cloneDeep(data);

  if (!sourceRecord || !sourceRecord.data)
    throw new Error('error while dragging, no dragged item received');

  const sourceTreeChildren = getParentChildrenByPath(
    nextTreeData,
    sourceTreePosition.parentPath,
  );

  // When moving an item we remove the item from it's former parent children array
  sourceTreeChildren.splice(sourceTreePosition.index, 1);
  nextTreeData = setTreeArrayInPath(
    nextTreeData,
    sourceTreePosition.parentPath,
    sourceTreeChildren,
  );

  // if source is at previous position than the target children
  // then the index should decrease
  const shouldUpdateTarget = isSourceBeforeTarget(
    sourceRecord.path,
    targetRecord.path,
  );
  const nextTargetPosition = shouldUpdateTarget
    ? combineWith
      ? {
          parentPath: updateTargetPath(sourceRecord.path, combineWith.path),
        }
      : getTreePosition(updateTargetPath(sourceRecord.path, targetRecord.path))
    : targetTreePosition;

  // add to target
  const { parentPath } = nextTargetPosition;
  const targetTreeChildren =
    getParentChildrenByPath(nextTreeData, parentPath) ||
    combineWith?.data ||
    targetRecord?.data;
  if (combineWith && targetTreeChildren) {
    const targetChildrensArray = targetTreeChildren[childrenKey];
    targetChildrensArray.push(sourceRecord.data);
  } else if (targetTreeChildren) {
    targetTreeChildren.splice(
      targetTreePosition.index + (down ? 1 : 0),
      0,
      sourceRecord.data,
    );
  }

  nextTreeData = setTreeArrayInPath(
    nextTreeData,
    parentPath,
    targetTreeChildren,
  );

  return nextTreeData;
};

export const combineWithTarget = () => {};

export const changePositionSameArray = ({
  targetTreePosition,
  sourceRecord,
  targetRecord,
  data,
}) => {
  let nextTreeData = cloneDeep(data);

  const targetTreeChildren = getParentChildrenByPath(
    nextTreeData,
    targetTreePosition.parentPath,
  );
  const nextChildren = arrayMove(
    targetTreeChildren,
    sourceRecord.index,
    targetRecord.index,
  );

  // mutates the tree. That's the reason of the clone above
  nextTreeData = setTreeArrayInPath(
    nextTreeData,
    targetTreePosition.parentPath,
    nextChildren,
  );

  return nextTreeData;
};
