/* eslint-disable react/prop-types */
import React from 'react';
import { Draggable } from 'react-beautiful-dnd';
import GripperVertical from '@elliemae/ds-icons/GripperVertical';
import { getDraggableProps } from '../utils';

// eslint-disable-next-line react/display-name
const DraggableItem = (
  renderers,
  // , instance
) => (props) => {
  const { record, index, isItemFocused } = props;
  const ItemComponent = renderers.item;
  const isDragDisabled =
    record?.parentNode?.data?.disableDragChildren ||
    record?.data?.disableDrag ||
    false;
  // const { draggedRecord } = instance.getInstance().state;

  // const isDragging = record.id === draggedRecord?.id;
  return (
    <Draggable
      isDragDisabled={isDragDisabled}
      key={`${record.id}-draggable`}
      draggableId={String(record.id)}
      index={index}
    >
      {(provided, snapshot) => {
        const { key, ...draggableProps } = getDraggableProps(
          props,
          provided,
          snapshot,
        );

        return (
          <ItemComponent
            key={key}
            {...draggableProps}
            // isDragging={isDragging}
            leftAddons={[
              <GripperVertical
                data-testid="drag-handle"
                key={`${record.id}-drag-handle`}
                {...provided.dragHandleProps}
                className="drag-handle"
                tabIndex={isItemFocused ? 0 : -1}
              />,
              ...(props.leftAddons || []),
            ]}
          >
            {props.children}
          </ItemComponent>
        );
      }}
    </Draggable>
  );
};

export default DraggableItem;
