import { useCallback, useMemo, useState } from 'react';
import { isEqual } from '@elliemae/ds-utilities/utils';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import DraggableItem from './renderers/DraggableItem';
import DroppableList from './renderers/DroppableList';
import {
  changePositionBetweenDifferentArrays,
  changePositionSameArray,
  getTreePosition,
} from './utils';

export const TreeDndPlugin = createInstancePlugin('tree-dnd', {
  state(instance) {
    const {
      originalData,
      props: { childrenKey = 'children', onOrderChange = () => null },
    } = instance;

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [draggedRecord, setDraggedRecord] = useState(null);

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const changeRecordsPosition = useCallback(
      ({ sourceRecord, targetRecord, combineWith, down }) => {
        let nextTreeData;

        const sourceTreePosition = getTreePosition(sourceRecord.path);
        let targetTreePosition = {};

        if (combineWith) {
          targetTreePosition = { parentPath: combineWith.path };
        } else if (targetRecord) {
          targetTreePosition = getTreePosition(targetRecord.path);
        } else {
          // there is no reordering
          // Reason: outside tree / outside drop target
          return originalData;
        }
        // if we are not combining elements we change the same array
        // if we combine elements, we change a different array (the children of the target)
        const changingSameArray =
          !combineWith &&
          isEqual(sourceTreePosition.parentPath, targetTreePosition.parentPath);

        if (changingSameArray) {
          nextTreeData = changePositionSameArray({
            sourceRecord,
            targetRecord,
            sourceTreePosition,
            targetTreePosition,
            data: originalData,
          });
        } else {
          nextTreeData = changePositionBetweenDifferentArrays({
            sourceRecord,
            targetRecord: combineWith || targetRecord,
            combineWith,
            sourceTreePosition,
            targetTreePosition,
            data: originalData,
            down,
          });
        }

        onOrderChange(nextTreeData, sourceRecord);
        // console.log('nextTreeData :>> ', nextTreeData);
        // console.log(
        //   `${sourceRecord?.data?.name} --> ${
        //     combineWith?.data?.name || targetRecord?.data?.name
        //   }`,
        // );
        return nextTreeData;
      },
      [originalData, childrenKey],
    );

    return {
      state: { draggedRecord },
      actions: { setDraggedRecord, changeRecordsPosition },
    };
  },
  renderers(renderers, instance) {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const item = useMemo(() => DraggableItem(renderers, instance), []);
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const list = useMemo(() => DroppableList(renderers, instance), []);
    return { ...renderers, item, list };
  },
});
